/* Reset some default styles */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body, html {
  height: 100%;
  font-family: 'Roboto', sans-serif;
  background-color: #181818; /* Dark background for the whole page */
  color: #f5f5f5; /* Light text color for contrast */
}

/* App container (Full height) */
.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Full viewport height */
}

/* Footer styling */
footer {
  background-color: #222; /* Dark footer background */
  color: white;
  text-align: center;
  padding: 10px 0;
  margin-top: auto; /* Push footer to bottom */
  box-shadow: 0 -4px 10px rgba(0, 0, 0, 0.5); /* Subtle shadow for the footer */
}

/* Content container that takes available space */
.content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

/* Navbar */
.navbar {
  background-color: #333; /* Darker navbar for contrast */
  color: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 25px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4); /* Subtle shadow effect */
}

.navbar .logo {
  font-size: 24px;
  font-weight: bold;
  color: #ff6f61; /* Accent color */
}

.navbar nav a {
  color: #f5f5f5;
  margin: 0 20px;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.navbar nav a:hover {
  color: #ff6f61; /* Accent color on hover */
}

/* Register Page */
/* Register Page with background image */
.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
  background-image: url('https://i.pinimg.com/736x/39/1e/7b/391e7ba4cb2c0752bf007c7bfb4ea143.jpg'); /* Your image URL */
  background-size: cover; /* Ensure the image covers the entire container */
  background-position: center; /* Keep the image centered */
  background-repeat: no-repeat; /* Prevent image repetition */
  background-attachment: fixed; /* Keep the background fixed while scrolling */
}

.register-page .form-container {
  background: rgba(0, 0, 0, 0.7); /* Semi-transparent dark background */
  padding: 30px;
  border-radius: 10px;
  max-width: 400px;
  width: 100%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.register-page h2 {
  text-align: center;
  color: #f5f5f5;
  margin-bottom: 20px;
  font-size: 28px;
}

.register-page .input-field {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  background-color: #333;
  border: 1px solid #444;
  border-radius: 5px;
  color: #fff;
}

.register-page .input-field:focus {
  border-color: #ff6f61; /* Accent color on focus */
  outline: none;
}

.register-page .submit-btn {
  width: 100%;
  padding: 12px;
  background-color: #ff6f61; /* Accent color */
  color: #fff;
  border: none;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}

.register-page .submit-btn:hover {
  background-color: #e55e4b; /* Darker shade for hover effect */
}

.register-page .text-center {
  text-align: center;
  margin-top: 20px;
}

.register-page .text-center a {
  color: #ff6f61; /* Accent color */
  text-decoration: none;
}

/* Filters */
.filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: #222;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.filters h6 {
  font-size: 18px;
  color: #f5f5f5;
}

.switch-icon {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  border: 1px solid #444;
  border-radius: 5px;
}

.switch-icon .anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.switch-icon .active-icon {
  color: #ff6f61; /* Accent color */
}

.switch-icon .inactive-icon {
  color: #aaa;
}

/* Responsive Design */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar nav {
    margin-top: 15px;
  }

  .navbar nav a {
    margin: 5px 0;
  }

  .register-page .form-container {
    width: 90%;
    padding: 20px;
  }
}
